import React from 'react';
import reactStringReplace from 'react-string-replace'

type Props = {
  children: React.ReactNode,
};

export function HighlightedText(props: Props) {
  let output = [props.children];
  output = reactStringReplace(output, 'â–²', () => <span className="triangle">â–²</span>)
  output = reactStringReplace(output, 'â—', () => <span className="circle">â—</span>)
  output = reactStringReplace(output, 'â—‹', () => <span className="circle">â—</span>)
  output = reactStringReplace(output, 'â¬¤', () => <span className="circle">â—</span>)
  output = reactStringReplace(output, 'â¬›', () => <span className="square">â– </span>)
  output = reactStringReplace(output, 'â– ', () => <span className="square">â– </span>)
  output = reactStringReplace(output, 'â¬Ÿ', () => <span className="pentagon">â¬Ÿ</span>)
  output = output.filter(el => el !== '')

  return <>{output}</>
}
